import { Team, CreateTeamDto, JoinTeamDto } from "@/entities/team/model/types";

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL || "http://localhost:8000";

export class TeamsService {
  static async createTeam(token: string, data: CreateTeamDto): Promise<Team> {
    console.log("Creating team:", data);
    try {
      const response = await fetch(`${API_BASE_URL}/teams`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Create team error:", errorText);
        throw new Error(errorText || "Failed to create team");
      }

      const team = await response.json();
      console.log("Team created successfully:", team);
      return team;
    } catch (error) {
      console.error("Error creating team:", error);
      throw error;
    }
  }

  static async getTeamById(token: string, teamId: number): Promise<Team> {
    console.log("Fetching team by ID:", teamId);
    try {
      const response = await fetch(
        `${API_BASE_URL}/teams/get_by_id/${teamId}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Get team error:", errorText);
        throw new Error(errorText || "Failed to get team");
      }

      const team = await response.json();
      console.log("Team fetched successfully:", team);
      return team;
    } catch (error) {
      console.error("Error fetching team:", error);
      throw error;
    }
  }

  static async getTeamByCode(token: string, code: string): Promise<Team> {
    console.log("Fetching team by code:", code);
    try {
      const response = await fetch(
        `${API_BASE_URL}/teams/get_by_code/${code}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Get team by code error:", errorText);
        throw new Error(errorText || "Failed to get team by code");
      }

      const team = await response.json();
      console.log("Team fetched successfully:", team);
      return team;
    } catch (error) {
      console.error("Error fetching team by code:", error);
      throw error;
    }
  }

  static async joinTeam(token: string, data: JoinTeamDto): Promise<Team> {
    console.log("Joining team with code:", data.code);
    try {
      const response = await fetch(`${API_BASE_URL}/teams/join`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Join team error:", errorText);
        throw new Error(errorText || "Failed to join team");
      }

      const team = await response.json();
      console.log("Successfully joined team:", team);
      return team;
    } catch (error) {
      console.error("Error joining team:", error);
      throw error;
    }
  }

  static async getMyTeams(token: string): Promise<Team[]> {
    console.log("Fetching user's teams");
    try {
      const response = await fetch(`${API_BASE_URL}/teams/my-teams`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Get my teams error:", errorText);
        throw new Error(errorText || "Failed to get teams");
      }

      const teams = await response.json();
      console.log("Teams fetched successfully:", teams);
      return teams;
    } catch (error) {
      console.error("Error fetching teams:", error);
      throw error;
    }
  }

  static async getTeamMembers(token: string, teamId: number): Promise<Team> {
    console.log("Fetching team members for team:", teamId);
    try {
      const response = await fetch(`${API_BASE_URL}/teams/${teamId}/members`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Get team members error:", errorText);
        throw new Error(errorText || "Failed to get team members");
      }

      const members = await response.json();
      console.log("Team members fetched successfully:", members);
      return members;
    } catch (error) {
      console.error("Error fetching team members:", error);
      throw error;
    }
  }
}
